import React, { Component } from 'react';
import raw from "raw.macro";
import ReactMarkdown from 'react-markdown';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import "bootstrap/dist/css/bootstrap.min.css";
import styles from './mobileitem.module.scss';


class MobileItem extends Component {
    state = {
        showModal: false
    }
    modalTrue = () => this.setState({
        showModal: true
    })

    modalFalse = () => this.setState({
        showModal: false
    })

    
    
    render() {
        
        const { title } = this.props;
        const { itemName } = this.props;
        const markdownDoc = raw(`../documents/${itemName}.md`);



        return (
            <>  
                <div className={styles.item} onClick={this.modalTrue}>
                    {title}
                </div>
                <Modal show={this.state.showModal} onHide={this.modalFalse} size="lg" centered>
                    <Modal.Header closeButton>
                    <Modal.Title>{title}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body style={{'max-height': 'calc(100vh - 210px)', 'overflow-y': 'auto'}}>
                                <ReactMarkdown>{markdownDoc}</ReactMarkdown>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.modalFalse}>Close</Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
        
        
    }
}

export default MobileItem;