import React, { Component } from 'react';
import Style from 'style-it';
import raw from "raw.macro";
import ReactMarkdown from 'react-markdown';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import "bootstrap/dist/css/bootstrap.min.css";
//import styles from './griditem.module.scss';


class GridItem extends Component {
    state = {
        showModal: false
    }
    modalTrue = () => this.setState({
        showModal: true
    })

    modalFalse = () => this.setState({
        showModal: false
    })

    
    
    render() {
        
        const { title } = this.props;
        const { top } = this.props;
        const { imgSrc } = this.props;
        const { iconSrc } = this.props;
        const { date } = this.props;
        const { desc } = this.props;
        const imgPath = `url(` + imgSrc + `)`;
        //const iconPath 
        const { itemName } = this.props;
        
        const markdownDoc = raw(`../documents/${itemName}.md`);



        if(top){
            return (
                <>  
                    <Style>
                        {`
                            .griditem {
                                color: white;
                                position: relative;
                                font-size: 1.5em;
                                text-align: center;
                                font-family: sans-serif;
                                background-color: #93cfe9;
                                margin: 8px 8px;
                                height: 280px;
                                box-shadow: 6px 6px 2px 1px rgba(72, 112, 106, .9);
                                z-index: 0;
                                grid-column-start: 1;
                                grid-column-end: 3;
                            }
                    
                            .griditem::before {
                                content: '';
                                position: absolute;
                                left: 0;
                                right: 0;
                                top: 0;
                                bottom: 0;
                                background-image: ` + imgPath +`;
                                background-repeat: no-repeat;
                                background-size: 100% 100%;
                                -moz-background-size: 100% 100%;
                                opacity: 0;
                                transition: opacity 1.5s;
                                z-index: -1;
                            }
                    
                            .griditem:hover::before {
                                opacity: 1;    
                            }
        
                            .griditem:hover {
                                color: rgba(255,255,255,0);
                                box-shadow: 0px 0px 2px 1px rgba(147, 207, 233, .9);
                            }

                            .griditem > #icon {
                                margin-left: 65%;
                                width: 160px;
                                height: 160px;
                            }

                            .griditem:hover > #icon {
                                visibility: hidden;
                            }

                            .title {
                                padding-left: 15px;
                                text-align: left;
                            }

                            .desc {
                                font-size: 0.7em;
                                text-align: left;
                                padding-left: 15px;
                            }

                            .date {
                                font-size: 0.5em;
                                text-align: left;
                                padding-left: 15px;
                            }

                            .top {
                                margin-left: 20px;
                            }
                        `}

                        <div className="griditem" onClick={this.modalTrue}>
                            <div className="title">{ title }</div>
                            <p className="date">{ date }       <b className="top">置顶</b></p>
                            <p className="desc">{ desc }</p>
                            <img id="icon" alt="" src={iconSrc}></img>
                        </div>
                    </Style>
                    <Modal show={this.state.showModal} onHide={this.modalFalse} size="lg" centered>
                        <Modal.Header closeButton>
                        <Modal.Title>{title}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body style={{'max-height': 'calc(100vh - 210px)', 'overflow-y': 'auto'}}>
                            <ReactMarkdown>{markdownDoc}</ReactMarkdown>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={this.modalFalse}>Close</Button>
                        </Modal.Footer>
                    </Modal>
                </>
            );
        } else {
            return (
                <>
                    <Style>
                        {`
                            .griditem {
                                color: white;
                                position: relative;
                                font-size: 1.5em;
                                text-align: center;
                                font-family: sans-serif;
                                background-color: #93cfe9;
                                margin: 8px 8px;
                                height: 280px;
                                box-shadow: 6px 6px 2px 1px rgba(72, 112, 106, .9);
                                z-index: 0;
                            }
                    
                            .griditem::before {
                                content: '';
                                position: absolute;
                                left: 0;
                                right: 0;
                                top: 0;
                                bottom: 0;
                                background-image: ` + imgPath +`;
                                background-repeat: no-repeat;
                                background-size: 100% 100%;
                                -moz-background-size: 100% 100%;
                                opacity: 0;
                                transition: opacity 1.5s;
                                z-index: -1;
                            }
                    
                            .griditem:hover::before {
                                opacity: 1;    
                            }
        
                            .griditem:hover {
                                color: rgba(255,255,255,0);
                                box-shadow: 0px 0px 2px 1px rgba(147, 207, 233, .9);
                            }

                            .griditem > #icon {
                                margin-left: 40%;
                                width: 160px;
                                height: 160px;
                            }

                            .griditem:hover > #icon {
                                visibility: hidden;
                            }

                            .title {
                                padding-left: 15px;
                                text-align: left;
                            }

                            .desc {
                                font-size: 0.7em;
                                text-align: left;
                                padding-left: 15px;
                            }

                            .date {
                                font-size: 0.5em;
                                text-align: left;
                                padding-left: 15px;
                            }
                        `}

                        <div className="griditem" onClick={this.modalTrue}>
                            <div className="title">{ title }</div>
                            <p className="date">{ date }</p>
                            <p className="desc">{ desc }</p>
                            <img id="icon" alt="" src={iconSrc}></img>
                        </div>
                    </Style>
                    <Modal show={this.state.showModal} onHide={this.modalFalse} size="lg" centered>
                        <Modal.Header closeButton>
                        <Modal.Title>{title}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body style={{'max-height': 'calc(100vh - 210px)', 'overflow-y': 'auto'}}>
                            <ReactMarkdown>{markdownDoc}</ReactMarkdown>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={this.modalFalse}>Close</Button>
                        </Modal.Footer>
                    </Modal>
                </>
            );
        }
    }
}

export default GridItem;